<script>
import axios from "axios";

export default {
  name: "form",

  data() {
    return {
      form: {
        username: localStorage.getItem("username") || "",
        name: "",
        gender: "",
        age: "",
        education: "",
        skills: "",
        internship: "",
        additionalInfo: "",
      },
      fileList: [],
      images: [
        // 
        "2_demo/fpic2.png",
        "2_demo/fpic3.png",
        "2_demo/fpic4.png",
      ],
      uploadProgress: 0, // 
      uploadInterval: null, // 
    };
  },
  mounted() {
    baguetteBox.run(".gangwei", {
      animation: "fadeIn",
    });
    const username = this.form.username;
    if (username && username !== "") {
      // this.uploadUrl = `/api/upload/${username}`;

      axios
        .get(`/api/interviewees/${username}`)
        .then((response) => {
          this.form = response.data;
        })
        .catch((error) => {
          console.error("", error);
        });
    }
  },
  computed: {
    uploadUrl() {
      return `/api/upload/${this.form.username}`;
    },
    uploadHeaders() {
      return {
        "X-Username": this.form.username,
      };
    },
  },
  components: {},
  methods: {
    beforeUpload(file) {
      const isPDF = file.type === "application/pdf";
      if (!isPDF) {
        this.$message.error("PDF");
      }
      return isPDF;
    },
    handleImageUpload(response, file) {
      console.log("", response);
      clearInterval(this.uploadInterval); // 
      this.uploadProgress = 100; // 100%
      this.$message.success("");
      this.$alert("", "", {
        confirmButtonText: "",
        type: "success",
      });
    },
    handleImageUploadError(err, file) {
      console.error("", err);
      this.$alert("Token", "", {
        confirmButtonText: "",
        type: "error",
      });
    },
    handleImageUploadProgress(event, file) {
      this.uploadProgress = Math.round((event.loaded * 100) / event.total);
      if (this.uploadProgress === 100) {
        this.$message({
          message: "",
          type: "info",
          duration: 3000, // 3
        });
      }
    },
    saveInfo() {
      const username = this.form.username;
      const apiUrl = `/api/interviewees/${username}`;
      const method = "put";

      axios[method](apiUrl, this.form)
        .then((response) => {
          this.$message({
            message: "",
            type: "success",
            duration: 1000, //  1 
          });
          setTimeout(() => {
            // this.$router.push("/archive");
          }, 1000); // 
        })
        .catch((error) => {
          console.error("", error);
          this.$message.error("");
        });
    },
  },
};
</script>
