<template>
  <div class="my">
    <div class="hd wow fadeInUp" data-wow-delay="0.3s" data-wow-duration="1s">
      <div class="wrap">
        <div class="name">
          <img :src="avatarUrl" alt="" />
          <h4>{{ username }}</h4>
        </div>
        <h5>
          Token: <i>{{ form.remainAdvanceToken }}</i>
        </h5>
        <h5>
          Token: <i>{{ form.remainAdvanceToken }}</i>
        </h5>
        <h5>
          : <i>{{ form.remainTime }}</i>
        </h5>
        <h5>
          : <i>{{ form.membershipEnd }}</i>
        </h5>

        <a class="bt" @click="centerDialogVisible = true">token</a>
      </div>
    </div>
    <div
      class="bd wrap wow fadeInUp"
      data-wow-delay="0.3s"
      data-wow-duration="1s"
    >
      <div class="lts">
        <div class="item">
          <ul>
            <li>
              <h3><i></i></h3>
              <dl>
                <dd>
                  <h4></h4>
                  <h5>{{ form.name }}</h5>
                </dd>
                <dd>
                  <h4></h4>
                  <h5>{{ form.gender }}</h5>
                </dd>
                <dd>
                  <h4></h4>
                  <h5>{{ form.age }}</h5>
                </dd>
              </dl>
            </li>
            <li>
              <h3><i></i></h3>
              <div class="boxscll">
                {{ form.education }}
              </div>
            </li>
            <li>
              <h3><i></i></h3>
              <div class="boxscll">
                {{ form.skills }}
              </div>
            </li>
            <li>
              <h3><i></i></h3>
              <div class="boxscll">
                {{ form.internship }}
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div class="lts">
        <div class="item">
          <ul>
            <li>
              <h3><i></i></h3>
              <div class="txt">
                {{ form.jobInfo }}
              </div>
            </li>
            <li>
              <h3><i></i></h3>
              <dl>
                <dd>
                  <h4></h4>
                  <h5>{{ form.companyName }}</h5>
                </dd>
                <dd>
                  <h4></h4>
                  <h5>
                    {{ form.companyInfo }}
                  </h5>
                </dd>
              </dl>
            </li>
            <li>
              <h3><i></i></h3>
              <dl>
                <dd>
                  <h4></h4>
                  <h5>{{ languageDisplay }}</h5>
                </dd>
                <dd>
                  <h4></h4>
                  <h5>
                    {{ form.additionalInfo }}
                  </h5>
                </dd>
              </dl>
            </li>
          </ul>
        </div>
        <div class="item item2">
          <h6></h6>
          <h3><i></i></h3>
          <select class="lsele" v-model="language" placeholder="">
            <option label="" value="zh-CN"></option>
            <option label="" value="en-US"></option>
          </select>
          <div class="btn">
            <a @click="saveSettings" class="on"></a>
            <a @click="resetSettings"></a>
          </div>
        </div>
      </div>
    </div>

    <el-dialog
      title="token"
      :visible.sync="centerDialogVisible"
      width="30%"
      center
    >
      <div>
        <el-form
          :model="shopForm"
          ref="shopForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="" prop="tokenType">
            <el-radio-group v-model="shopForm.tokenType" size="medium">
              <el-radio-button
                v-for="token in tokens"
                :label="token"
                :key="token"
                >{{ token }}</el-radio-button
              >
            </el-radio-group>
          </el-form-item>

          <el-form-item label="" prop="nums">
            <el-input-number
              v-model="shopForm.nums"
              :min="1"
              :max="999"
            ></el-input-number>
          </el-form-item>
          <el-form-item label="" prop="money">
            <span>{{ tokenIntro }}</span>
          </el-form-item>
        </el-form>
      </div>

      <span slot="footer" class="dialog-footer">
        <el-button @click="centerDialogVisible = false"> </el-button>
        <el-button type="primary" @click="centerDialogVisible = false"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
